    commitQuestion() {
      //
      let gxrArr = [];
      if (this.ContributionPeople.length != 0) {
        this.ContributionPeople.forEach((ele, i, arr) => {
          gxrArr.push(ele.fbrxm + "," + ele.fbrbh + "," + ele.qrgs);
        });
      }
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          if (this.fwzlValue == 0) {
            this.$alert("", "", {
              confirmButtonText: "",
              type: "warning",
              callback: action => {}
            });
            return;
          }
          if (this.fwzlValue <= 3 && !this.cpsm) {
            this.$alert("", "", {
              confirmButtonText: "",
              type: "warning",
              callback: action => {}
            });
            return;
          }
          closeQuestion({
            wid: this.wid,
            zlpf: this.fwzlValue,
            gxrData: gxrArr.join("|"),
            sfjj: "",
            jjsm:
              $("#summernoteT").summernote("code") == "<p><br></p>"
                ? ""
                : $("#summernoteT").summernote("code"),
            cpsm: this.fwzlValue <= 3 ? this.cpsm : "",
            gssfrk: this.gssfrk
          }).then(({ data }) => {
            if (data.state == "success") {
              this.innerVisible = false;
              this.$alert(data.msg, "", {
                confirmButtonText: "",
                type: "success",
                callback: action => {
                  this.queryQuestion(this.wid);
                  this.queryAnswers(this.wid);
                  this.queryBtnAuth(this.wid);
                  this.queryProcess(this.wid);
                }
              });
            } else {
              this.$alert(data.msg, "", {
                confirmButtonText: "",
                type: "error"
              });
            }
          });
        })
        .catch(() => {});
    },
    queryProcess(wid) {
      queryProcess({
        //
        wid: wid,
        isTs: ""
      }).then(({ data }) => {
        if (data.state == "success") {
          this.questionProcess = data.data;
          this.questionProcess.forEach((ele, i, arr) => {
            if (ele.flag == 1) {
              this.questionProcess[i].status = "process";
            } else if (ele.flag == 2) {
              this.questionProcess[i].status = "success";
            }
          });
          this.lchjList = this.questionProcess.slice(
            0,
            this.questionProcess.length - 2
          );
        }
      });
    },

    queryQuestion(wid, xmtd) {
      //   
      this.qusetionInfo = {};
      this.wtlbmc = "";
      this.wtlymc = "";
      this.hjgs = 0;
      queryQuestion({
        wid: wid
      }).then(({ data }) => {
        if (data.state == "success") {
          this.qusetionInfo = data.data;
          this.wtly = data.data.wtly;
          this.cnjsrq = data.data.qwjjrq;
          document.title = "-" + data.data.bt;
          if (this.qusetionInfo.fbzt == 1) {
            $("#summernoteT").summernote("destroy");
          }
          if (xmtd == 1) {
            isgcXmtdbyWt({
              wid: this.qusetionInfo.wid
            }).then(({ data }) => {
              if (data.state == "success") {
                this.isgcXmtdbyWt = data.data;
              }
            });
          }
          if (this.qusetionInfo.wtlb != "") {
            getResponsibleTaskList({
              name: "ProblemType",
              code: this.qusetionInfo.wtlb
            }).then(({ data }) => {
              if (data.state == "success") {
                this.wtlbmc = data.data;
              }
            });
          }
          if (this.qusetionInfo.wtly != "") {
            getResponsibleTaskList({
              name: "ProblemType",
              code: this.qusetionInfo.wtly
            }).then(({ data }) => {
              if (data.state == "success") {
                this.wtlymc = data.data;
              }
            });
          }

          //    
          if (data.data.fbzt == 1) {
            this.zlpf = JSON.parse(data.data.zlpf);
            queryContributionPeople({
              //
              wid: this.wid
            }).then(({ data }) => {
              if (data.state == "success") {
                if (data.data != null && data.data.length != 0) {
                  this.yxGXR = data.data;
                  this.yxGXR.forEach((ele, i, arr) => {
                    if (ele.gs.split(".")[0] == "") {
                      ele.gs = 0 + ele.gs;
                    }
                    this.hjgs += parseFloat(ele.gs);
                  });
                } else {
                  this.yxGXR = [];
                }
              }
            });
          }
          try {
            this.qusetionInfo.nr = decodeURIComponent(
              this.qusetionInfo.nr
            ).replace(
              /gcgl\/sys\/emapcomponent\/file\/getFileByToken/g,
              "emap/sys/emapcomponent/file/getFileByToken"
            );
          } catch (err) {
            this.qusetionInfo.nr = this.qusetionInfo.nr;
          }
        }
      });
    },
    // 
    queryAnswers(wid) {
      this.questionReply = [];
      queryAnswers({
        wid: wid,
        isSolution: this.isCjwt ? true : ""
      }).then(({ data }) => {
        if (data.state == "success") {
          this.questionReply = data.data;
          if (data.data != null && data.data.length != 0) {
            this.questionReply.forEach((ele, i, arr) => {
              try {
                ele.nr = decodeURIComponent(ele.nr).replace(
                  "/gcgl/sys/emapcomponent/file/getFileByToken/",
                  "/emap/sys/emapcomponent/file/getFileByToken/"
                );
              } catch (err) {
                ele.nr = ele.nr;
              }
            });
          }
        }
      });
    },
    //  
    queryBtnAuth(wid) {
      queryBtnAuth({
        wid: wid
      }).then(({ data }) => {
        if (data.state == "success") {
          this.btnShwon = data.data;
          this.xgqwjjrq = data.data.qwjjrq;
        }
      });
    },
      //
    queryLabel(wid) {
      this.sftj = [];
      queryLabel({
        wid: wid
      }).then(({ data }) => {
        if (data.state == "success") {
          this.wtbq = data.data;
          this.wtbq.forEach((ele, i, arr) => {
            if (ele.sjly == 0) {
              ele.type = "info";
            } else {
              ele.type = "";
            }
            if (ele.sjly == 2){
              this.sftj.push(ele.dm);
            }
          });
        }
      });
    },

    getCrowdYwx() {
      this.$get(this.API.getCrowdYwx, {}).then(res => {
        if (res.state == "success") {
          this.CrowdYwx = res.data;
        }
      });
    },
    getCrowdCp(ywxbm) {
      this.$get(this.API.getCrowdCp, { ywxbm: ywxbm }).then(res => {
        if (res.state == "success") {
          this.CrowdCp = res.data;
        }
      });
    }
  },
  watch: {
    crowdVisible(n, o) {
      if (!!n) {
        this.crowdxqData.zbjzrq = GetDateStr(7);
      }
    },
    crowdType(n, o) {
      if (n == 2) {
        this.getCrowdYwx();
      }
    }
  },
  activated() {

	},
  components: {
    pagination,
    itemChoose,
    zdfzrChoose,
    sqjsForm,
    twDialog,
    xxDialog,
    xmtdcylog,
    uploadFile
  }
};
</script>
<style scoped>
.project-question {
  width: 95%;
  margin: 0 auto;
  padding: 10px;
}

.question-type {
  text-align: center;
  width: 100px;
}
.questionOperate {
  font-size: 12px;
  white-space: nowrap;
  opacity: 0;
}
.questionOperate > span:hover {
  text-decoration: underline #888;
}

.question-type span.el-icon-question {
  font-size: 32px;
  color: #f56c6c;
}
.question-type span.el-icon-success {
  font-size: 30px;
  color: #67c23a;
}

.Question {
  padding: 10px 0;
}
div.el-form-item {
  margin-bottom: 8px !important;
}
.send_crowd div.el-form-item {
  margin-bottom: 25px !important;
}
/*   detail*/
.project-question-detail {
  padding: 5px 10px;
  background: #fff;
}
.project-question-detail li {
  border-bottom: 1px solid #f1f1f1;
}
.project-question-detail-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #f1f1f1;
}
.project-question-detail .question-reply {
  display: inline-block;
  width: 38px;
  height: 26px;
  border-radius: 3px;
  text-align: center;
  line-height: 24px;
  background: #3ba7f5;
  color: #fff;
  font-size: 14px;
}
.project-question-detail-top .question-content {
  width: 90%;
}
.project-question-detail-top .question-content-SC {
  width: 92%;
  color: #888;
}
.question-content-SC .el-button,
.question-content-SC .el-tag {
  height: 28px;
}
.question-content-SC .el-tag {
  line-height: 28px;
}
.question-content .question-info-front {
  color: #a8a8a8;
  font-size: 12px;
}
.question-content > p {
  margin: 0 0 3px 0 !important;
  width: 100%;
  white-space: nowrap;
  font-size: 14px;
}
.question-content > p > span {
  display: inline-block;
  min-width: 25%;
  white-space: nowrap;
  font-size: 14px;
  color: #363748;
}
.question-content > p > span:nth-of-type(2) {
  width: 40%;
}

.project-question-detail-bottom > div {
  width: 100%;
  overflow: auto;
  padding: 10px 5px 10px 80px;
}
.isshown-query:hover {
  cursor: pointer;
}

.bg-active {
  background: #1997d7;
  color: #fff !important;
}
.question-state {
  text-align: center;
  width: 13%;
}
.question-state > span:nth-of-type(1) {
  padding: 3px 8px;
  border-radius: 2px;
  font-size: 14px;
  display: inline-block;
}
.ygb-color {
  color: #7ece64 !important;
  background: rgba(126, 206, 100, 0.2);
}
.clz-color {
  color: #f56c6c;
  background: rgba(245, 108, 108, 0.2);
}

.question-evaluate {
  padding: 10px;
}
.question-evaluate > p {
  margin-top: 10px !important;
  color: #888;
  font-size: 12px;
}
.question-rate {
  margin: 10px 0;
}
.question-rate label {
  margin-bottom: 0;
}
.question-rate:after {
  content: "";
  display: block;
  clear: both;
}
.question-rate > p {
  display: flex;
  align-items: center;
  float: left;
}
.question-steps {
  padding: 5px 10px;
  background: #f4f6f9;
}
.question-textarea {
  width: 700px;
  margin: 0 auto;
}
.question-textarea > p {
  width: 125px;
  text-align: right;
  padding-right: 12px;
  font-weight: 700;
}
.question-textarea > p::before {
  content: "*";
  color: #f56c6c;
  margin-right: 4px;
}
.CrowdRwxx {
  margin-bottom: 10px;
}
.CrowdRwxx span:nth-of-type(1),
.CrowdRwxx span:nth-of-type(3) {
  font-weight: 700;
}
.Crowdrwmc {
  display: inline-block;
  width: 350px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: bottom;
}
/* */
.question-sqdyz-title:before {
  content: "*";
  color: #f00;
  padding: 0 2px;
}
.item-wtbq {
  float: left;
  padding: 2px 0;
}
.item-wtbq-tag {
  display: inline-block;
  width: 95%;
  white-space: pre-wrap;
}
.filter-bt {
  display: inline-block;
  width: 100px;
  vertical-align: top;
  text-align: right;
  font-weight: 700;
}
.question_transmit .span_label {
  display: inline-block;
  width: 110px;
}

.isRedColor {
  color: #f00;
  font-weight: 700;
}

.send_crowd img {
  width: 100%;
}
.send-crowd_topic {
  border: 1px solid #e4eaec;
  background: #f3f7f9;
  border-radius: 3px;
  height: 40px;
  line-height: 40px;
  padding: 0 6px;
}

.crowd-rqgz h4 {
  font-weight: 700;
  font-size: 13px;
  margin: 10px 0 !important;
}
.crowd-rqgz p {
  font-size: 12px;
  text-indent: 2em;
}
</style>



